/* Adjust user timestamp */
.user-timestamp {
  font-size: 0.9rem;
  color: #3333336a;
  display: block;
  text-align: right;
  margin-bottom: -12px; /* Adjust spacing between timestamp and message */
}

/* Adjust bot timestamp */
.bot-timestamp {
  font-size: 0.9rem;
  color: #3333336a;
  display: block;
  text-align: left;
  margin-bottom: -12px; /* Adjust spacing between timestamp and message */
}

.minimize-button {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
  color: black;
  display: flex;
  align-items: center;
}

.notification-badge {
  position: absolute;
  top: -5px;
  right: -5px;
  background: rgb(0, 166, 255);
  color: white;
  border-radius: 50%;
  padding: 5px 10px;
  font-size: 0.75em;
}





@keyframes heartbeat {
  0% {
    transform: scale(1);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  50% {
    transform: scale(1.1);
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
}